.sectionPadding {
  padding:200px 264px 130px;
    @media screen and (max-width: 1920px) {
        padding: 150px 200px 130px;
      }
      @media screen and (max-width: 1536px) {
        padding: 150px 146px 100px;
      }
      @media screen and (max-width: 1280px) {
        padding: 150px 108px 100px;
      }
      @media screen and (max-width: 1200px) {
        padding: 150px 126px 90px;
      }
      @media (max-width: 1024px) {
        padding: 150px 100px 90px;
      }
      @media (max-width: 820px) {
        padding: 120px 50px 90px;
      }
      @media (max-width: 768px) {
        padding: 150px 50px;
      }
      @media (max-width: 602px) {
        padding: 130px 40px;
      }
      @media (max-width: 430px) {
        padding: 90px 8px 50px;
      }
}


.heading-width{
  width: 73%;
  @media screen and (max-width: 1920px) {
      width: 100%;
  }
  @media screen and (max-width: 1536px) {
      width: 100%;
  }
  @media screen and (max-width: 1440px) {
      width: 100%;
  }
  @media screen and (max-width: 1433px) {
      width: 93%;
  }
  @media screen and (max-width: 1366px) {
      width: 100%;
  }
  @media screen and (max-width: 1280px) {
      width:80%;
  }
  @media screen and (max-width: 1024px) {
      width: 100%;
  }
  @media screen and (max-width: 768px) {
      width: 100%;
  }
  @media screen and (max-width: 430px) {
      width: 100%;
  }
}


h1{
  margin-bottom: 3rem;
  @media screen and (max-width: 1536px) {
    margin-bottom: 2rem;
  }
  @media screen and (max-width: 1440px) {
    margin-bottom: 1.8rem;
  }
  @media screen and (max-width: 430px) {
    margin-bottom: 1rem;
  }
}
h2{
  margin: 2.6rem 0;
  @media screen and (max-width: 1536px) {
    margin: 2rem 0;
  }
  @media screen and (max-width: 1440px) {
    margin: 1.8rem 0;
  }
  @media screen and (max-width: 430px) {
    margin: 1rem 0;
  }
}
p{
  margin-bottom: 2.6rem;
  @media screen and (max-width: 1536px) {
    margin-bottom: 2rem;
  }
  @media screen and (max-width: 1440px) {
    margin-bottom: 1.8rem;
  }
  @media screen and (max-width: 430px) {
    margin-bottom: 1rem;
  }
}


.size88 {
  font-size: 88px;

  @media (max-width: 1650px) {
    font-size: 64px;
  }

  @media (max-width: 1280px) {
    font-size: 54px;
  }

  @media (max-width: 768px) {
    font-size: 50px;
  }

  @media (max-width: 480px) {
    font-size: 42px;
  }

  @media (max-width: 384px) {
    font-size: 34px;
  }
}

li::marker {
  font-family: "RedHat Bold" !important;
}
